	var config = require('../../../config')
import 'mint-ui/lib/style.css'
import { MessageBox ,Indicator} from 'mint-ui';
    export default {
        data(){
            return {
                    prouuctdata:{},
                    weixinpayconfig:{}
            }
        },
        mounted: function() {
                       //
                            this.geteditproductinfo()
            },
            methods:{
            geteditproductinfo(){
              Indicator.open({
                                         text:'',
                                         spinnerType: 'fading-circle'
                                           });
                                                                  setTimeout(() => Indicator.close(), 1000);
                    var good_id=    this.$route.query.good_id;
                    var user_id=    this.$route.query.user_id||0;
                    var order_id=    this.$route.query.order_id||0;
                    var order_sn=    this.$route.query.order_sn||'';
                    var is_finalpayment=   1;
                    var	that =this;
                    var dd =new FormData();
                    dd.append('token',storeWithExpiration.get('token'))
                    dd.append('good_id',good_id)
                    dd.append('order_id',order_id)
                    dd.append('order_sn',order_sn)
                    dd.append('is_finalpayment',is_finalpayment)
                    axios.post(decodeURIComponent(config.dev.env.default_domain_api)+'/index/getproductbyid', dd)
                                .then(function(response) {
                                if(response.status=='200'){
                                      return response.data;
                                }
                    }).then(function(json) {
                    if('2000'==json.code){
                            that.prouuctdata = json.data;
                            that.weixinpayconfig  = json.jsApiParameters;
                        }else{
                             Indicator.open({
                             text:json.message,
                             spinnerType: 'fading-circle'
                        });
                         setTimeout(() => Indicator.close(), 1000);
                        }
                    }).catch(function(ex) {

                    });
            },
            //JS api 
            jsApiCall()
            {
            var _that=this;
                WeixinJSBridge.invoke(
                    'getBrandWCPayRequest',
                    this.weixinpayconfig,
                    function(res){
                        WeixinJSBridge.log(res.err_msg);
                        if(res.err_code==0){
                               MessageBox.alert("").then(action => {
                                                                         _that.$router.go(-1)
                                                                         });
                        }else if(res.err_code==-1){
                                 MessageBox.alert("").then(action => {
                                                                                                     _that.$router.go(0)
                                                                                                     });

                        }else if(res.err_code==-2){
                                 MessageBox.alert("").then(action => {
                                                                                                     _that.$router.go(0)
                                                                                                     });

                        }
                    }
                );
            },
            callpay()
            {
                Indicator.open({
                  text: '...',
                  spinnerType: 'fading-circle'
                });
                 setTimeout(() => Indicator.close(), 2000);
                if (typeof WeixinJSBridge == "undefined"){
                    if( document.addEventListener ){
                        document.addEventListener('WeixinJSBridgeReady', jsApiCall, false);
                    }else if (document.attachEvent){
                        document.attachEvent('WeixinJSBridgeReady', jsApiCall);
                        document.attachEvent('onWeixinJSBridgeReady', jsApiCall);
                    }
                }else{
                    this.jsApiCall();
                }
            }
            },
            computed:{

            }
    }



