var routes = [
    {path: '/', meta: {title: ''}, component: index},
    {path: '/fabu', name: 'fabu', meta: {title: ''}, component: fabu},
    {path: '/goodsproductlists', name: 'goodsproductlists', meta: {title: ''}, component: goodsproductlists},
    {path: '/newsindexlists/:cate_id(\\d+)?',name:'newsindexlists', meta: {title: ''}, component: newsindexlists},
    {
        path: '/goodsproductdetail/:goods_id(\\d+)',
        name: 'goodsproductdetail',
        meta: {title: ''},
        component: goodsproductdetail
    },
    {path: '/staffinquiry', name: 'staffinquiry', meta: {title: ''}, component: staff_inquiry},


    {
        path: '/headermessagedetail/:article(\\d+)/:user_id(\\d+)?',
        name: 'newsdetail',
        meta: {title: ''},
        component: headermessagedetail
    },


    {path: '/shaked', name: 'shaked', meta: {title: ''}, component: shaked},

    // {path: '/index/:cat_id?',name:'index', meta: {title: ''}, component: index},
    {path: '/index/:cat_id?', name: 'index', meta: {title: ''}, component: index},
    {path: '/shop/:userid?', name: 'shopindex', meta: {title: ''}, component: index},
    {path: '/foucswebsite/:userid?', name: 'foucswebsite', meta: {title: ''}, component: foucswebsite},

    {path: '/find/:userid(\\d+)?/:type(\\d+)?', name: 'find', meta: {title: ''}, component: paimaiquan},

    {path: '/cacse/:goods_id?', name: 'cacse', meta: {title: ''}, component: cacse},


    {path: '/statistics/:userid(\\d+)?/:type(\\d+)?', name: 'statistics', meta: {title: ''}, component: statistics},


    {path: '/mymain/:userid(\\d+)?/:type(\\d+)?', name: 'mymain', meta: {title: ''}, component: paimaiquan},
    {
        path: '/paymenting/:lastnum\\_:good_id(\\d+)\\.html',
        name: 'paymenting',
        meta: {title: ''},
        component: paymenting
    },
    {
        path: '/paymenting/orderpaymenting',
        name: 'orderpaymenting',
        meta: {title: ''},
        component: orderpaymenting
     },
    {path: '/paymenting', name: 'paymentingold', meta: {title: ''}, component: paymenting},
    {path: '/paipinmessage', name: 'paipinmessage', meta: {title: ''}, component: paipinmessage},
    {path: '/usercenter/mycollectionproductlist', component: mycollectionproductlist},
    {
        path: '/usercenter', component: usercenterapp,
        children: [
            {path: '', meta: {title: ''}, component: usercenter},
            {path: 'index', meta: {title: ''}, component: usercenter},
            {
                path: 'orderlist/:status?/:issale?',
                name: 'orderlist',
                component: orderlist
            },
            {
                path: 'setting',
                component: usersetvue
            },
            {
                path: 'myfoucs',
                component: myfoucs
            },
            {
                path: 'foucsme',
                component: foucsme
            },
            {
                path: 'paying/:price(\\d+)/:type(\\d+)',
                name: 'paying',
                component: paying
            },
            {
                path: 'index',
                component: usercenter
            },
            {
                path: 'signature',
                component: msignature
            },
            {
                path: 'contacts',
                component: contacts
            },
            {
                path: 'weixinnumber',
                component: weixinnumber
            },
            {
                path: 'mobile',
                component: mobile
            },
            {
                path: 'myqrcode/:userid(\\d+)?',
                component: myqrcode
            },
            {
                path: 'mesagecenter',
                component: mesagecenter
            },
            {
                path: 'myassets',
                component: myassets
            },
            {
                path: 'withdraw',
                component: withdraw
            },
            {
                path: 'sendmessage',
                component: sendmessage
            },
            {
                path: 'helper',
                component: helper
            },
            {
                path: 'address',
                component: address
            },
            {
                path: 'company_individual',
                meta: {title: ''},
                component: company_individual
            },
            {
                path: '*',
                component: usercenter
            }
        ]
    },
    {
        path: '/usersellindex', name: 'usersellindex', component: usercenterapp,
        children: [
            {
                path: 'index',
                name: 'usersellindexindex',
                meta: {title: ''},
                component: user_sellindex
            },
            {
                path: '',
                meta: {title: ''},
                component: user_sellindex
            },
            {
                path: 'productlist',
                meta: {title: ''},
                component: productlist
            },
            {
                path: 'editproduct',
                name: 'editproductbyid',
                meta: {title: ''},
                component: editproduct
            },
            {
                path: 'orderlistsale/:status?/:issale?',
                name: 'orderlistsale',
                meta: {title: ''},
                component: orderlistsale
            },
            {
                path: 'mumberupgrade',
                meta: {title: ''},
                component: mumberupgrade
            },
            {
                path: 'authentication',
                meta: {title: ''},
                component: authentication
            },
            {
                path: 'companyverifyProcess',
                meta: {title: ''},
                component: companyverifyProcess
            },
            {
                path: 'mumbercheck',
                meta: {title: ''},
                component: mumbercheck
            },
            {
                path: 'user_privacy',
                meta: {title: ' '},
                component: user_privacy
            },
            {
                path: 'auction_manage',
                meta: {title: ' '},
                component: auction_manage
            },
            {
                path: 'user_service',
                meta: {title: ' '},
                component: user_service
            },
            {
                path: 'gerenverifyProcess',
                meta: {title: ''},
                component: gerenverifyProcess
            },
            {
                path: 'individual',
                meta: {title: ''},
                component: gerenverifyProcess_individual
            },
            {
                path: 'gerenverifyPayment',
                meta: {title: ''},
                component: gerenverifyPayment
            },
            {
                path: 'mumber_notice',
                meta: {title: ''},
                component: mumber_notice
            },
            {
                path: 'setting',
                component: usersellindexsetting
            },
            {
                path: 'myqverfitymobile',
                component: myqverfitymobile
            },
            {
                path: 'punish',
                component: punish
            },
            {
                path: 'warm_prompt',
                component: warm_prompt
            },
            {
                path: '*',
                meta: {title: ''},
                component: user_sellindex
            }
        ]
    },
    {path: '*', meta: {title: ''}, component: index}
];
import {weixinlogin, urlrefer} from "./assets/js/common_function.js"
var router = new VueRouter({
    mode: 'history',
    routes: routes,
    scrollBehavior (to, from, savedPosition) {
        return {x: 0, y: 0}
    }
});
router.beforeEach((to, from, next) => {
            var  flagislogin = window.storeWithExpiration.get('token') || '';
            if (is_weixn() && !flagislogin) {
                weixinlogin(to.fullPath, from, next)
            } else {
                next()
            }
    }
)
//sync(store, router)
const vm = new Vue({
    store: store,
    router: router,
    render: function (h) {
        return h(App);
    }
})

vm.$mount('#app')



